import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { csv, scaleLinear, max, format, extent } from 'd3';
import { useData } from './useData';
import { AxisBottom } from './AxisBottom';
import { AxisLeft } from './AxisLeft';
import { Marks } from './Marks';
import './style.css';

const width = 960;
const height = 500;
const margin = { top: 20, right: 30, bottom: 65, left: 90 };
const xAxisLabelOffset = 50;
const yAxisLabelOffset = 35;

// Define a mapping of column names to labels
const yAxisLabels = {
  'Economy': 'Economy',
  'Social_': 'Social Support',
  'Generosity': 'Generosity',
  'Freedom': 'Freedom',
  'Corruption': 'Perception of Corruption',
}

const ScatterPlotDepression = () => {
  const data = useData();
  const [yAttribute, setYAttribute] = useState('Economy');

  if (!data) {
    return <pre>Loading...</pre>;
  }

  const innerHeight = height - margin.top - margin.bottom;
  const innerWidth = width - margin.left - margin.right;

  const xValue = d => d.Allages;
  const xAxisLabel = 'Rate of Depression (%) (All Ages)';

  const yValue = d => d[yAttribute];
  const yAxisLabel = yAxisLabels[yAttribute];

  const siFormat = format('.2s');
  const xAxisTickFormat = tickValue => siFormat(tickValue).replace('G', 'B');

  const xScale = scaleLinear()
    .domain(extent(data, xValue))
    .range([0, innerWidth])
    .nice();

  const yScale = scaleLinear()
    .domain(extent(data, yValue))
    .range([innerHeight, 0]);  

  const handleChange = event => {
    setYAttribute(event.target.value);
  }

  return (
    <>
      <label for="y-select">Y:</label>
      <select id="y-select" value={yAttribute} onChange={handleChange}>
        <option value="Economy">Economy</option>
        <option value="Social_">Social Support</option>
        <option value="Generosity"> Generosity</option>
        <option value="Freedom"> Freedom </option>
        <option value="Corruption">Perception of Corruption</option>
      </select>
      <svg width={width} height={height}>
        <g transform={`translate(${margin.left},${margin.top})`}>
          <AxisBottom
            xScale={xScale}
            innerHeight={innerHeight}
            tickFormat={xAxisTickFormat}
            tickOffset={5}
          />
          <text
            className="axis-label-1"
            textAnchor="middle"
            style={{ fontSize: '24px' }}
            transform={`translate(${-yAxisLabelOffset - 20},${innerHeight/2}) rotate(-90)`}

          >
            {yAxisLabel}
          </text>
          <AxisLeft yScale={yScale} innerWidth={innerWidth} tickOffset={5} />
          <text
            className="axis-label-1"
            x={innerWidth / 2}
            y={innerHeight + xAxisLabelOffset}
            textAnchor="middle"
          >
            {xAxisLabel}
          </text>
          <Marks
            data={data}
            xScale={xScale}
            yScale={yScale}
            xValue={xValue}
            yValue={yValue}
            tooltipFormat={xAxisTickFormat}
            circleRadius={7}
          />
        </g>
      </svg>
    </>
  );
};

export default ScatterPlotDepression;