import React, { useState, useEffect } from 'react';
import { csv } from 'd3';

const csvUrl =
      'https://gist.githubusercontent.com/Nikithakatta30/5af6f9143327728ac153cb333f996cfc/raw/7a20a1f88f2d5b98fb455d05bf03a9a89bc0b682/Project.csv'
export const useData = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    const row = d => {
      d.Happiness_Score = +d.Happiness_Score;
      d.Allages = +d.Allages;
      d.Year = +d.Year;
      return d;
    };
    csv(csvUrl, row).then(setData);
  }, []);
  
  return data;
};